___CSS_LOADER_EXPORT___.push([module.id, `* :not(dialog) {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    user-select: none;
}

html {
    font-size: 16px;
}

body {
    font-family: Arial, Helvetica, sans-serif;
    background-color: whitesmoke;
    display: flex;
}

button {
    padding: 0.25rem 0.5rem;
    font-size: 0.8rem;
    text-align: center;
    border: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
}

button:hover {
    background-color: #ddd;
}

button:active {
    background-color: #fff;
}

.add-new-task {
    margin-bottom: 1rem;
}

li {
    list-style: none;
}

#nav-menu {
    background-color: #eeeeee;
    min-height: 100vh;
    min-width: 200px;
    padding: 2rem 1rem;
}

#create-list-button {
    margin-bottom: 1rem;
}

#list-menu li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;
}

#list-menu li div:hover {
    background-color: #ddd;
}

#list-menu li div:active {
    background-color: #fff;
}

#wrapper {
    width: 100%;
    padding: 2rem;
}

#wrapper h1 {
    margin-bottom: 1rem;
}


#add-task form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.card {
    padding: 12px;
    border: 1px dotted black;
    margin-bottom: 0.5rem;
    display: grid;
    grid-template-columns: 5fr auto auto;
    gap: 0.5rem;
    place-items: center;

}

.card h2{
    text-decoration: underline;
    font-weight: 500;
    font-size: 1rem;
    justify-self: start;
}

.description {
    grid-column: span 2;    
}

.date {
    font-size: 1rem;
    writing-mode: vertical-rl;
    font-family: 'Courier New', Courier, monospace;
}

.priority {
    padding: 0.25rem 0.5rem;
    font-size: 0.75rem;
    text-align: center;
    border: 1px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
}

.priority-1 {
    background-color: greenyellow;
}

.priority-2 {
    background-color: yellow;
}

.priority-3 {
    background-color: red;
    color: whitesmoke;
}

dialog {
    border: 1px solid black;
    box-shadow: 0 0 12px #00000085;

}

#add-list-form,
form {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    gap: 1rem;
}

.dialog-buttons {
    display: flex;
    gap: 0.5rem;
}

input {
    padding: 0.25rem;
    accent-color: black;
}

fieldset {
    border: none;
}

fieldset>div {
    padding-left: 0.5rem;
}

.block-label-input {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
}

textarea {
    font-family: Arial, Helvetica, sans-serif;
    padding: 0.25rem;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,UAAU;IACV,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,yCAAyC;IACzC,4BAA4B;IAC5B,aAAa;AACjB;;AAEA;IACI,uBAAuB;IACvB,iBAAiB;IACjB,kBAAkB;IAClB,uBAAuB;IACvB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,yBAAyB;IACzB,iBAAiB;IACjB,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,WAAW;IACX,aAAa;AACjB;;AAEA;IACI,mBAAmB;AACvB;;;AAGA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,wBAAwB;IACxB,qBAAqB;IACrB,aAAa;IACb,oCAAoC;IACpC,WAAW;IACX,mBAAmB;;AAEvB;;AAEA;IACI,0BAA0B;IAC1B,gBAAgB;IAChB,eAAe;IACf,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,yBAAyB;IACzB,8CAA8C;AAClD;;AAEA;IACI,uBAAuB;IACvB,kBAAkB;IAClB,kBAAkB;IAClB,uBAAuB;IACvB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,6BAA6B;AACjC;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,qBAAqB;IACrB,iBAAiB;AACrB;;AAEA;IACI,uBAAuB;IACvB,8BAA8B;;AAElC;;AAEA;;IAEI,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,SAAS;AACb;;AAEA;IACI,aAAa;IACb,WAAW;AACf;;AAEA;IACI,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,YAAY;AAChB;;AAEA;IACI,yCAAyC;IACzC,gBAAgB;AACpB","sourcesContent":["* :not(dialog) {\n    box-sizing: border-box;\n    padding: 0;\n    margin: 0;\n    user-select: none;\n}\n\nhtml {\n    font-size: 16px;\n}\n\nbody {\n    font-family: Arial, Helvetica, sans-serif;\n    background-color: whitesmoke;\n    display: flex;\n}\n\nbutton {\n    padding: 0.25rem 0.5rem;\n    font-size: 0.8rem;\n    text-align: center;\n    border: 1px solid black;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\nbutton:hover {\n    background-color: #ddd;\n}\n\nbutton:active {\n    background-color: #fff;\n}\n\n.add-new-task {\n    